import slide from 'components/slide/index'
import rankList from 'components/rankList/index'
import http from 'js/http'

export default {
  name: "index",
  components: {
    slide,
    rankList
  },
  data() {
    return {
      dataList: [1, 2, 3, 4, 5],
      nodeList: [],
      nodeTab: [],
      currentNodeId: '',
      page: 1,
      counttime: "",
      loadShow: true,
      total:''
    };
  },
  deactivated() {

  },
  methods: {
    selectTab(item) {
      if (item.id === this.currentNodeId) return
      this.currentNodeId = item.id
      localStorage.setItem("nodeRankType", item.id);
      this.page = 1
      this.nodeList = []
      /*this.loadShow = true
      this.$refs.vueLoad.onBottomLoaded();
      this.getNodeList()*/
      this.total = ''
      this.$refs.my_scroller.finishInfinite(false);
    },
    handleBottom() {
      if (this.currentNodeId === ''){
        this.getNodeTab(()=>{
          this.handleBottom()
        })
        return
      }
      if (this.total!==''&&this.nodeList.length >= parseInt(this.total)){
        this.$refs.my_scroller.finishInfinite(true);
        return
      }

      http.post('/node/vote', {
        id: this.currentNodeId,
        page: this.page,
        page_size: 10
      }, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        // console.log(this.nodeList)
        this.nodeList = this.nodeList.concat(res.content.list)
        // console.log(this.nodeList,res.content)
        this.counttime = res.content.counttime
        this.total = res.content.count
        this.page++
        // done()
        this.$refs.my_scroller.finishInfinite(false);

      })
    },
    getNodeTab(cb) {
      http.post('/node', {}, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.nodeTab = res.content
        this.currentNodeId = localStorage.getItem('nodeRankType') || res.content[0].id
        cb()
      })
    },
    getNodeList() {
      http.post('/node/vote', {
        id: this.currentNodeId,
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.nodeList = this.nodeList.concat(res.content.list)
        this.counttime = res.content.counttime
        if (this.nodeList.length < parseInt(res.content.count)) {
          this.$refs.vueLoad.onBottomLoaded();
        } else {
          this.$refs.vueLoad.onBottomLoaded(false);
        }
      })
    },
  },
  created() {
    // this.getNodeTab()
  },
  mounted() {
  }
}



